html {
    scroll-behavior: smooth;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Nunito Sans', sans-serif;
}


.sticky {
    position: -webkit-sticky;
    /* For Safari */
    position: sticky;
    top: 70%;
    /* Adjust this value based on how far down the element should "stick" */
    z-index: 1000;
    /* Optional: Ensure the element stays above other content */
    background-color: white;
    /* Optional: Maintain background color */
}

::-webkit-scrollbar {
    width: 8px;
    height: 6px;
}

/* Scrollbar track */
::-webkit-scrollbar-track {
    background: rgb(216 216 216);
}

/* Scrollbar thumb */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 5px;
}

/* Scrollbar thumb on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.srollBoxNav ::-webkit-scrollbar {
    display: none;
}